import { Country } from "./Country";
import { Gender } from "./Gender";
import "../stores";
export class Client {
    constructor() {
        this.gender = Gender.x;
        this.firstName = "";
        this.lastName = "";
        this.fullName = "";
        this.title = "";
        this.academicDegree = "";
        this.birthday = "01/09/1990";
        this.occupation = "";
        this.email = "";
        this.phone = "";
        this.mobile = "";
        this.country = Country.AT;
        this.zipCode = "";
        this.city = "";
        this.address = "";
        this.bank = "";
        this.iban = "";
        this.bic = "";
    }
    static byId(id) {
        let client = new Client();
        client.id = id;
        client.gender = Gender.m;
        client.firstName = "First " + id;
        client.lastName = "Lastname" + id;
        client.fullName = client.lastName + " " + client.firstName;
        client.email = id + "mail@maily.com";
        client.title = "Ing.";
        client.academicDegree = "BSc.";
        client.occupation = "Software Engineer";
        client.phone = "01111111111" + id;
        client.mobile = "000000000000" + id;
        client.country = Country.AT;
        client.zipCode = "0000";
        client.city = "City" + id;
        client.address = "Randomstreet " + id;
        return client;
    }
    static list() {
        return [...Array(30).keys()].map(index => (Client.byId(index)));
    }
}
